.LargeColor {
    width: 100%;
    /* color: white; */
    box-shadow: 0px 0px 5px grey;
    height: 80vh;
    border-top-right-radius: 5px;
    border-radius: 5px;
    display: grid;
    grid-auto-rows: 4fr 1fr;
    box-shadow: 0px 0px 5px grey;
}

.LargeColor-color {
    height: 100%;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}

.LargeColor-detail {
    color: #363D3D;
    margin: auto 0;
    font-size: 40px;
    padding-left: 5%;
}


@media (max-width: 570px) {
    .LargeColor {
        min-width: 220px;
    }
}

